import React, { Component } from "react";
import styled from "styled-components";

export const Div = styled.div`
  position: absolute;
  top: 0;
  left: -140px;
  width: 140px;
  height: 100px;
  border: 1px solid #e9e8e7;
  transition: all 500ms;
`;

class Sidebar extends Component {
  render() {
    const {} = this.props;
    return <Div>123</Div>;
  }
}

export default Sidebar;
